.icons-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 10px;
  gap: 10px;
  max-width: 200px;
  margin: auto;
  -webkit-align-items: center;
          align-items: center;
  /* position: center; */
}

.subheader {
  font-style: italic;
  font-size: 20px;
  font-weight: normal !important;
}
.link-item {
  max-width: 100px;
  max-height: 100px;
}

.link-item:hover {
  border-style: solid;
  transition: all 0.3s ease 0s !important;
  border-radius: 5px;
  /* padding: 10px; */
}

.about {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 30px;
  gap: 30px;
}

